
.mainBody {
    position: relative;
}
section {
    display: block;
    clear: both;
}
.secondBnnerForDetail {
    width: 100%;
    height: 350px;
    max-width: 1240px;
    margin: 0 auto;
    overflow: hidden;
    background-position: center center;
    background-repeat: no-repeat;
    background-size: cover;
    position: relative;
    overflow: hidden;
}
.cutline {
    border-bottom: 1px solid #aaa;
}
.pdt40 {
    padding: 40px 0px;
}
.sbw1240 {
    width: 90%;
    max-width: 1240px;
    margin: 0 auto;
}
h2 {
    font-size: 3em;
    line-height: 40px;
    display: inline-block;
    font-weight: 300;
}
.listChangesBox {
    float: right;
}
.listChangesBox ul {
    height: 40px;
}
.listChangesBox ul .active {
    background-color: #cc3434;
}
.listChangesBox .listAll {
    background-image: url(../assets/list_all.png);
    background-position: 112px center;
    background-repeat: no-repeat;
    font-size: 1.3em;
}
.listChangesBox ul li {
    float: left;
    width: 140px;
    height: 40px;
    line-height: 40px;
    background-color: #dedede;
    text-indent: 1em;
    margin-left: 20px;
}
.listChangesBox .listTime {
    background-image: url(../assets/list_time.png);
    background-position: 112px center;
    background-repeat: no-repeat;
    position: relative;
    font-size: 1.3em;
}
.listChangesBox ul .active a {
    cursor: default;
}
.listChangesBox ul li a {
    color: #fff;
    display: block;
}
.listChangesBox .listClass {
    background-image: url(../assets/list_class.png);
    background-position: 112px center;
    background-repeat: no-repeat;
    position: relative;
    font-size: 1.3em;
}



