
.bannerBox{
	width: 100%;
	height: auto;
	position: relative;
	overflow: hidden;
}
.bannerBox .arrow-left{
    background-image: url(../assets/prev.png);
    background-position: center center;
    background-repeat: no-repeat;
    position: absolute;
    left: 0px;
    top: 50%;
    margin-top: -50px;
    width: 50px;
    height: 100px;
    z-index: 5;
}
.bannerBox .arrow-right{
    background-image: url(../assets/nextv.png);
    background-position: center center;
    background-repeat: no-repeat;
    position: absolute;
    right: 0px;
    top: 50%;
    margin-top: -50px;
    width: 50px;
    height: 100px;
    z-index: 5;
}
#banners{
	width: 500%;
}
#banners li{
	width: 20%;
	float: left;
}
#banners li img{
	width: 100%;
}



