
section {
  display: block;
  clear: both;
}
.secondBnner {
    width: 100%;
    height: 350px;
    max-width: 1240px;
    margin: 0 auto;
    overflow: hidden;
    background-repeat: no-repeat;
    background-position: center center;
    background-size: cover;
}
.cutline {
    border-bottom: 1px solid #aaa;
}
.pdt40 {
    padding: 40px 0px;
}
.sbw1240 {
    width: 90%;
    max-width: 1240px;
    margin: 0 auto;
}
h2 {
    font-size: 3em;
    line-height: 40px;
    display: inline-block;
    font-weight: 300;
}
.baseGiftList {
    width: 100%;
}
.baseGiftList li {
    width: 33.3%;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    float: left;
    margin-bottom: 20px;
}
a.baseGiftListLink{
    display: block;
    border: 1px solid #fff;
}
.baseGiftList .giftImgBox {
    background-size: cover;
    background-position: center center;
    background-repeat: no-repeat;
}
.baseGiftList .giftWordBox {
    border-top: 1px dashed #ededed;
    margin: 0px 5px;
}
.baseGiftList .giftWordBox .mainGiftTitle {
    font-size: 2em;
    line-height: 2em;
    color: #cc3434;
}
.baseGiftList .giftWordBox span {
    display: block;
    width: 100%;
    font-size: 1.4em;
    line-height: 2em;
    color: #999;
    text-align: center;
}



